import React from 'react';
import { Flex, Stack, Text, Box, useMediaQuery, Heading, Spacer } from '@chakra-ui/react';

const About = () => {
    const [isLargeScreen] = useMediaQuery("(min-width: 600px)");

    return (
        <Stack>
            <Heading fontWeight="bold" fontSize="2xl" bgGradient="linear(to-tr, teal.300,yellow.400)" bgClip="text">about.</Heading>
            <Spacer />
            <Flex direction={isLargeScreen ? "row" : "column"}>
                <Box width={isLargeScreen ? "580px" : "270px"} border="1px" borderColor="blackAlpha.300" borderRadius="lg" bg="blackAlpha.300">
                        <Box p={6}>
                            <Text fontSize="sm">
                                Passionate learner formerly working as a Data Analyst. Experienced tackling complex problems with <b>Python</b>, <b>R</b>, and <b>Power BI</b>.
                                Focusing on front-end development with <b>React</b> and <b>Redux</b>, though experienced in putting together full-stack applications (see below).
                            </Text>
                        </Box>
                </Box>      
            </Flex>
        </Stack>
    );
};

export default About;
